<script>
import CommonHeader from "@/views/common/CommonHeader.vue";
import { defineComponent, ref } from "vue";
import {kakaoSignup, logout} from "../api/auth.js";
import { Comment, Edit, ChevronLeft } from "@vicons/fa";
import { useRoute } from "vue-router";
import { useMessage } from "naive-ui";
import router from "@/routes/index.js";

const pageInfo = {
  title: 'Sign up',
  category: '   .',
};

const userInfo = ref({
  user_id: '',
  password: '',
});

export default defineComponent({
  created() {
    logout();
  },
  components:{
    CommonHeader,
    Comment,
    Edit,
    ChevronLeft,
  },
  setup() {
    const route = useRoute();
    const message = useMessage();
    const agreeAll = ref(false);
    const agree1 = ref(false);
    const agree2 = ref(false);
    const signup= ()=>{
      if(agree1.value && agree2.value){
        kakaoSignup(route);
      }else{
        message.create(
            ",     .",
            {duration: 4000, type: "info"}
        );
      }
    }
    const goSignup= ()=>{
    }
    const goSignin= ()=>{
      router.push('/login');
    }
    const checkAll=(value)=>{
      agree1.value=value;
      agree2.value=value;
    }
    const checkOne=()=>{
      if(agree1.value && agree2.value){
        agreeAll.value=true;
      }else{
        agreeAll.value=false;
      }
    }
    return {
      userInfo,
      pageInfo,
      agreeAll,
      agree1,
      agree2,
      signup,
      goSignup,
      goSignin,
      checkAll,
      checkOne,
    }
  }
});

</script>

